import React, { useState } from 'react'

import { LongValues } from '../contexts/LongValues';
import { useNavigate } from 'react-router-dom';
const Guides = () => {
    //searchbar
    // Step 2: Create a state variable to store the search query
    const [searchQuery, setSearchQuery] = useState('');
    document.title = 'STI Troubleshoot'
    const [search, setsearch] = useState(false);
    const history = useNavigate()

    // Step 3: Create a function to handle changes in the search input
    const handleSearchInputChange = (event) => {
      setSearchQuery(event.target.value);
      
    };
 
    function gotoHome(){
      history('/')
    }
    // Step 4: Create a function to handle the form submission
    const handleSearchSubmit = (event) => {
      event.preventDefault();

      setsearch(true)
      
      console.log(`Searching for: ${searchQuery}`)

      setTimeout(() => {
      setsearch(false);
      }, 2000); // Simulate a 2-second delay
    };
    //searchbar
    const guideData = [];
    for (let i = 1; i <= 40; i++) {
      const guide = {
        title: `Guide ${i}`,
        category: `Category ${i}`,
        content: `${i}.) ${LongValues.content}`,
        date:`Date ${i}`,
        owner: `Owner ${i}`,
        
      };
      guideData.push(guide);
    }

  return (
    <>
    <div id='cta-home'>
      <button onClick={gotoHome}>Home</button>
      <button>Send Ticket</button>
    </div>

    <div id='g-navigationcomponent'>
        <div id='middle-form'>
          <form onSubmit={handleSearchSubmit}>
            <input
              type="text"
              placeholder= "search here..."
              value={searchQuery}
              onChange={handleSearchInputChange}
            />
            <button type="submit">
             {search ? 'finding...' : 'find'}
            </button>
          </form>
          {}
        </div>
    </div>

    <div id='g-maincontainer'>
      <div id='g-container'>
         {guideData.map((guide, index) => (
          <div id='guides' key={index}>
            <div id='guide-container'>
              <div id='image-video'>image-video</div>
              <div id='g-header'>
                <div>
                  <span id='g-title'>{guide.title}</span>
                </div>
                <div id='user-cat'>
                  <span id='g-category'>{guide.category}</span>
                  <span id='g-category'>{guide.date}</span>
                  <span id='g-category'>{guide.owner}</span>
                </div>
              </div>
              <div id='contents'>{guide.content}</div>
            </div>
          </div>
          ))}
       </div>
    </div>
     
    </>

  )
}

export default Guides